export default {
  name: "FooterTwo",
  data(){
    return {
      scrollBtn: false
    }
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },

  methods: {

    handleScroll () {

      if (window.scrollY > 70) {
        this.scrollBtn = true
      }
      else if (window.scrollY < 70) {
        this.scrollBtn = false
      }
    },

    scrollTop () {
      window.scrollTo(0, 0);
    }


  }
}
